}
#wrapper {
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    background-color: #343434;
    border-radius: 20px 20px 20px 20px;
    width: 90%;
    max-width: 850px;
    padding: 1.5rem;
    /* box-shadow: 0px 10px 20px rgba(0,0,0,0.1); */
    box-shadow:12px 12px greenyellow;
    border: 3px solid greenyellow;
}
.formWrapper {
    display: flex;
    justify-content: center;
}
form {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 1rem;
    font-size: .9rem;
}
form > input {
    padding: 1rem;
    width: 90%;
    border-radius: 10px;
    border: 1px solid #E7E7E7;
    font-weight: bold;
    font-size: 1.5rem;
    color: #e8e8e8;
    background-color: #3C3C3B;
    border: none;
    box-shadow: 0px 10px 20px rgba(0,0,0,0.1); 
    /* box-shadow:6px 3px greenyellow;  */
}
input::placeholder {
    color: #8C8C8C;
}
input:focus {
    outline: none !important;
  }
.input-msg {
    color: red;
    font-size: 0.75rem;
    visibility: hidden;
}
.label {
    text-transform: uppercase;
    align-self: flex-start;
    padding: 0 0 0 1rem;
   color: #181818;
   font-weight: bold;
}
.divider {
    display: flex;
    align-items: center;
  }
  .line {
    flex-grow: 1;
    height: 5px;
    background-color: greenyellow;
  }
.arrow {
    padding: 0 30px;
    transition: 0.5s;
    cursor: pointer;
    color: greenyellow;
}
  .arrow:hover {
    animation: a 0.5s 1 linear;
  }
  @keyframes a {
    0% {
      transform: scale(0.7, 1.3);
    }
    25% {
      transform: scale(1.3, 0.7);
    }
    50% {
      transform: scale(0.7, 1.3);
    }
    75% {
      transform: scale(1.3, 0.7);
    }
    100% {
      transform: scale(1, 1);
    }
  }
  .result-style {
    color: greenyellow;
    padding: 1rem;
    text-shadow: none;
} 
.result {
    font-weight: bold;
    font-style: italic;
    font-size: 1rem; 
    color: #141414; 
    text-shadow:1px 2px greenyellow;
}
@media(min-width: 750px) {
    .result {
        font-size: 4rem; 
        text-shadow:3px 6px greenyellow;
    }
}